.icon {
  position: relative;
  display: block;
  width: 100%;
  padding-bottom: 100%;

  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  text-decoration: none;

  &__tooltip {
    position: absolute;
    right: 100%;
    top: 50%;
    transform: translateY(-50%);
    display: block;
    width: max-content;
    padding-right: 20px;

    font-weight: 700;
    font-size: 12px;
    line-height: 16px;
    color: #fff;
    opacity: 0;
    letter-spacing: 2px;
    transition: opacity 0.3s;
    }

    @include onDesktop {
      &:hover &__tooltip {
        opacity: 1;
        pointer-events: all;
        }
    }

  &--menu {
    background-image: url(../images/menu.svg);
    transition: all 0.3s;

    &:hover {
      background-image: url(../images/menu_hover.svg);
    }
  }

  &--phone {
    background-image: url(../images/phone.svg);
  }

  &--cross {
    background-image: url(../images/cross.svg);
  }
}
